import {useState} from 'react';
import Layout from '../../../components/Layout';
import Sidebar from '../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';


const Orders = () => {
  const navigate = useNavigate();

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-62px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome}/>
                <p className='ml-2 font-bold'>Orders</p>
              </div>
              <div className='px-[2rem] text-2xl text-gray-100 mt-8'>Comming soon</div>
            </div>
            <Sidebar />
        </div>
    </Layout>
  );
}

export default Orders;